import React from 'react';
import { connect } from 'react-redux';
import About from './About';
import TextLink from 'components/TextLink';

let AboutH18Cards = props => {
      return (
        <About title="Helsinki 2018 Cards and NoT - the Non-Internet of Things">

          <h4>The Story of the Cardish</h4>
          <TextLink to="#cutthecrap" style={{fontSize:"50%"}}>Tech Specs</TextLink>

          <p>
            It turns out that many things which have identities are not smart
            phones.  Many things lack both human biometrics and secure cryptographic
            co-processing enclaves.  Such things often lack GPS tracking signals,
            and you can not easily run their fingerprints through an AFIS system.
          </p>
          <p>
            Yet they exist, are unique, are identifiable, can be owned or
            guarded, die or pass on, and in every sense qualify for identity.
          </p>
          <p>
            Despite their limitations, these cards continue to exist - quite happily -
            sitting and carding around with their cardly friends, hanging out in wallets
            and on desktops, nestled down in the cracks of sofa cushions or
            resting, forgotten, on taxi seats or the tables of conference venues.
          </p>
          <p>
            Every one of these cards was born at
            <TextLink to ="http://www.nuntapun.com/">Nuntapun Printing</TextLink>
            (located
              <TextLink href=",19.64z/data=!4m5!3m4!1s0x30da305971986779:0x380cd0d910a54e67!8m2!3d18.7558546!4d98.9681593" >
              here</TextLink>
            ). they are destined to travel the world before
            becoming irrelevant, lost, or discarded.  This is the nature of
            cardish life and death.
          </p>
          <p>
            Current tools are too difficult to use to model the life of these
            noble little cardish folk.  While they have an identity, are unique
            in the world, and could tell stories to their cardly children - there
            is still a digital divide between their reality and the world of
            managed, let alone decentralized, identity systems.
          </p>
          <p>
            The cardish represent the forgotten, the disenfranchised, and
            the discluded identities.
          </p>
          <p>
            Any identity management ecosystem capable of managing my international
            KYC information or protecting me from the intrusions of an abusive
            surveilance state should be up to the task of managing the identity
            of the cardish.
          </p>
          <p>
            Using the cardish as stand-ins for more serious and weighty identities,
            such as those of People, Pets, Corporations, and Governments, we hope
            to map out the technical details of what it takes to track, manage,
            and sustain identity.
          </p>
          <p>
            Along the way we hope to learn a lot about the world of DIF and
            understand which tools are card-positive and which are cardist.
            Which identity systems have implicit reliance upon the biometrics
            or cryptographic enclaves of machines manufactured by a limited set
            of manufacturers?
          </p>
          <p>
            The cardish represent the pinnacle of those who will never, ever,
            be able to express agency over their private keys.  I have yet to
            meet a card that can even sign on to a macbook pro using touch id.
            I have also yet to meet a card remotely interested in such pedestrian
            matters as managing a complex system of private public-key-infrastructure.
          </p>

          <a name="cutthecrap" />
          <br />
          On the more serious side....
          <ol>
            <li></li>
          </ol>
        </About>
      )
}

AboutH18Cards = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutH18Cards);


export default AboutH18Cards;



