import React from 'react';
import { connect } from 'react-redux';

let NotSupported = props => {
    console.log("NOT SUPPORTED PROPS:",props);
    const { method } = props;
    return (
      <div>
        <h4>{method} DID resolution method not yet supported.</h4>
        As of August, 2018, only h18 lookups are supported.  We are in the
        process of deploying an instance of the <a href="">Universal Resolver</a>.
        This work is part of our operational analysis and support research.
        Decentralized identity requires strong and widespread deployments
        of DID resolution, along with the ability to integrate informal and
        non-W3C registered DID methods, such as h18.
        <br />
        <a href="">Read more about the Korsimoro/Universal Resolver effort.</a>
      </div>
    )
}


NotSupported = connect(
    state =>({
        ...state.server
    }),
    dispatch => ({
    })
)(NotSupported);


export default NotSupported;



