import React from 'react';
import { connect } from 'react-redux';
import About from './About';
import TextLink from 'components/TextLink';

let AboutUseCases = props => {
    return (
      <About title="STF2/DSTF Reference Identity Pool">

        <h4>STF2/DSTF Reference Identity Pool &amp; Use Case Definitions</h4>

        <p>
          Talking about identity, in all of it's myriad facets, can be either
          stupendously simple or incredibly complicated.
        </p>

        <p>
          For anyone reading this, it is likely that your sense of identity,
          your sense of <i>I</i>, is so obvious and essential that we give it
          names like <i>Soul</i> and <i>Consciousness</i>.  We live by a code of
          <TextLink to="https://en.wikipedia.org/wiki/Cogito,_ergo_sum">
          Cogito, ergo sum</TextLink> and all that.
        </p>

        <p>
          Yet, once we start asking ourselves about others, and about the
          things around us, the world gets very complicated, very quickly.
        </p>

        <p>
          Edge cases spring up, issues of personal agency arise.  While it
          remains clear that each of us <i>Sapiens</i> is unique, and has an
          identity - is the same true of our pets, our livestock, our children? 
        </p>

        <ol>
          <li><a href="/?sn=00646">Pet</a></li>
          <li><a href="/?sn=04096">Government</a></li>
          <li><a href="/?sn=49152">Dependent</a></li>
          <li><a href="/?sn=52348">Guardianship</a></li>
          <li>Organizations
            <ol>
              <li><a href="/?sn=64738">Corporation</a></li>
              <li><a href="/?sn=65534">Accreditation Bureau</a></li>
              <li><a href="/?sn=65535">NGO</a></li>
            </ol>
          </li>
        </ol>
      </About>
    )
}

AboutUseCases = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutUseCases);


export default AboutUseCases;



